@import url('/static/portal/css/default.css');

@import 'base/util'; //mixins
@import '../../commons/scss/breakpoint'; //반응형 플러그인
@import 'base/variables'; //환경 설정
@import 'base/color'; // colors
@import 'components/ico'; // icons
//@import 'breakpoint'; //반응형 플러그인
//@import 'base/heading';//해딩스타일
@import 'components/popup';//팝업

@include breakpoint($lg){
	body {
		overflow: hidden;
	}
}
#content {
	margin-top: 0 !important;
}
#wrap {
	padding-top: 0;
}
#header {
	position: fixed;
	background-color: rgba(0, 14, 51, 0.7);
	backdrop-filter: blur(4px);
	border-bottom: none !important;
	.globalHeader {
		@include breakpoint($lg){
			.container {
				border-bottom: 1px solid rgba(255, 255, 255, 0.2);
				ul {
					li + li::before {
						background-color: rgba(255, 255, 255, 0.3);
					}
					a {
						color: #fff;
						font-weight: 400;
					}
				}
			}
		}
	}
	@include breakpoint($lg){
		.toggle-set {
			> .search {
				background-color: #3B63C4;
			}
		}
	}
	.toggle-set {
		.all-nav-toggle::before {
			color: #fff;
		}
	}
	&.over {		
		@include breakpoint($lg){
			background-color: #fff;
			.logo a {
				background: url(/static/portal/img/main/logo.png) 50% no-repeat;
			}
			.gnb {
				> ul > li > a {
					color: $black;
				}
				> ul > li:hover > a, 
				> ul > li.on > a, 
				> ul > li.active > a {
					color: $color-point;
				}
			}
			.globalHeader {
				.container {
					border-bottom: 1px solid #E6E6E6;
					ul {
						li + li::before {
							background-color: rgba(0, 85, 150, 0.3);
						}
						a {
							color: $color-point;
							font-weight: 500;
						}
					}
				}
			}
			@include breakpoint($lg){
				.toggle-set {
					> .search {
						background-color: $color-point;
						&::before {
							color: #fff;
						}
					}
					.all-nav-toggle::before {
						color: $color-point;
					}
				}
			}
		}
	}
	.logo a {
		background: url(/static/portal/img/common/logo.png) 50% no-repeat;
	}
	.g-info {
		@include breakpoint($lg){
			.global {
				ul {
					li {						
						a {
							color: #fff;
						}
					}
				}
			}
		}
	}
	@include breakpoint($lg){
		.gnb-stiky & {
			background-color: #fff;
			box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.15);
			@include breakpoint($lg){
				.globalHeader {
					display: none;
				}
				.logo a {
					background: url(/static/portal/img/main/logo.png) 50% no-repeat;
				}
				.g-info > .search {
					width: 75px;
					height: 72px;
					top: 0;
				}
				.gnb {
					> ul > li > a:hover {
						color: $color-point;
					}
					> ul > li > a,
					.all-nav-toggle::before {
						color: $black;
					}
				}
				.toggle-set {
					> .search {
						background-color: $color-point;
						&::before {
							color: #fff;
						}
					}
					.all-nav-toggle::before {
						color: $color-point;
					}
				}
			}
		}
	}
}

.gnb-stiky #header {
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.15);
	.globalHeader {
		display: none;
	}
}

#header .globalHeader .container ul a,
.m-g-info .global ul a {
	color: #fff;
}

.gnb {
	> ul {
		@include breakpoint($lg){
			> li {
				> a {
					color: #fff;
				}
			}
		}
	}
}

.section-nav {
	position: fixed;
	left: 32px;
	bottom: 50%;
	z-index: 200;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
	gap: 16px;
	transition: all 0.3s;
	@include breakpoint($smd){
		display: none;
	}
	a {
		display: flex;
		padding: 7px;
		color: rgba(255, 255, 255, 0.8);
		font-size: 12px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
		text-transform: uppercase;
		transition: all 0.5s;
		* {
			transition: all 0.5s;
		}
		i {
			position: relative;
			@include circle(6px);
			background-color: rgba(255, 255, 255, 0.6);
		}
		&.active {
			transition: none;
			animation: pulse-white 2s infinite;
			border-radius: 50%;
			i {
				@include circle(10px);
				background-color: #fff;
				&::before {
					content: "";
					position: absolute;
					inset: 0;
					outline: 8px solid rgba(255, 255, 255, 0.2);
					border-radius: 20px;
				}
			}
		}
	}
	&--color {
		a {
			//color: #000;
			i {
				background-color: rgba(59, 99, 196, 0.6);
			}
			&.active {
				animation: pulse-blue 2s infinite;
				i {
					background-color: rgba(59, 99, 196, 1);
					&::before {
						outline: 8px solid rgba(59, 99, 196, 0.2);
						background-color: rgba(59, 99, 196, 1);
					}
				}
			}
		}
	}
}


@keyframes pulse-white {
	0% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.7);
	}
	
	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
	}
	
	100% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
	}
}

@keyframes pulse-blue {
	0% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(59, 99, 196, 0.7);
	}
	
	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(59, 99, 196, 0);
	}
	
	100% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(59, 99, 196, 0);
	}
}


.section {
	@include breakpoint($lg){
		//min-height: 1080px;
		height: 100vh;
	}	
	h2 {
		color: #131547;
		font-size: rem(48) !important;
		font-weight: 600 !important;
		margin-bottom: 100px !important;
		@include breakpoint($mdw){
			font-size: rem(40) !important;
			margin-bottom: 40px !important;
			padding-top: 30px;
		}
		@include breakpoint($sm){
			font-size: rem(30) !important;
			margin-bottom: 20px !important;
			padding-top: 30px;
		}
	}
	section {
		width: 1680px;
		max-width: 100%;
		margin: 0 auto;
		padding: 0 120px;
		@include breakpoint($mdw){
			padding: 0 40px;
		}
		@include breakpoint($sm){
			padding: 0 20px;
		}
	}
}

.section1-visual {
	height: 100%;
	@include breakpoint($lg){
		a {
			&:focus {
				position: relative;
				&::before {
					content: "";
					display: block;
					width: 100%;
					height: calc(100% - 146px);
					outline: 2px dotted #000;
					outline-offset: -2px;
					position: absolute;
					top: 146px;
					left: 0;
					z-index: 9;
				}
			}
		}
	}
	@include breakpoint($mdw){
		height: 700px;
	}
	@include breakpoint($sm){
		height: 600px;
	}
	.swiper-slide {
		position: relative;
		.img-visual {
			display: block;
			background-size: cover;
			//background-size: auto 100%;
			//background-size: auto 100%;
			background-position: 50% 0; 
			//background-repeat: no-repeat;
			transition: all 1s;
			transform: scale(1.1) !important;
			overflow: hidden;
			height: 100%;
		}
		@include breakpoint($mdw){
			.img-visual {
				height: 700px;
			}
		}
		@include breakpoint($sm){
			.img-visual {
				height: 600px;
			}
		}
		&.swiper-slide-active {
			.img-visual {
				transform: scale(1) !important;
				//transition: transform 4s;
				transition: 3.5s;
				opacity: 1;
			}
			//-webkit-animation: visual-bg 3s ease-out 1;
			//animation: visual-bg 3s ease-out 1;
			//-webkit-animation-iteration-count: 10;
			//animation-iteration-count: 10;
			//-webkit-animation: visual-bg 3s cubic-bezier(0.45, 0.05, 0.55, 0.95) 1;
			//animation: visual-bg 3s cubic-bezier(0.45, 0.05, 0.55, 0.95) 1;
			.s1-t1 {
				-webkit-animation: visual-txt1 2s cubic-bezier(0.45, 0.05, 0.55, 0.95) 1;
				animation: visual-txt1 2s cubic-bezier(0.45, 0.05, 0.55, 0.95) 1;
				-webkit-animation-delay: 1s;
				animation-delay: 1s;
				opacity: 0;
				-webkit-animation-fill-mode: forwards;
				animation-fill-mode: forwards
			}
			.s1-t2 {
				-webkit-animation: visual-txt2 2s cubic-bezier(0.45, 0.05, 0.55, 0.95) 1;
				animation: visual-txt2 2s cubic-bezier(0.45, 0.05, 0.55, 0.95) 1;
				-webkit-animation-delay: 2s;
				animation-delay: 2s;
				-webkit-animation-fill-mode: forwards;
				animation-fill-mode: forwards;
				opacity: 0
			}
		}
		.s1-txt-set {
			position: absolute;
			bottom: 129px;
			padding-left: 121px;
			text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.3);
			width: 100%;
			max-width: 1680px;
			@include breakpoint($smd){
				left: 10px;
				width: calc(100% - 20px);
				padding-left: 0;
			}
		}
		.s1-t1 {
			color: #fff;
			font-size: rem(56);
			font-style: normal;
			font-weight: 700;
			line-height: normal;
			//text-transform: uppercase;
			@include breakpoint($mdw){
				font-size: rem(40);
			}
			@include breakpoint($sm){
				font-size: rem(30);
			}
		}
		.s1-t2 {
			margin-top: 30px;
			color: #fff;
			font-size: rem(28);
			font-style: normal;
			font-weight: 500;
			line-height: normal;
			//text-transform: uppercase;
			@include breakpoint($sm){
				font-size: rem(20);
				margin-top: 15px;
			}
		}
		/*&::before {
			content: "";
			position: absolute;
			inset: 0;
			//backdrop-filter: blur(6px);
			//transition: all 0.5s;
			z-index: 1;
		}*/
	}
	.swiper-slide-fully-visible {
		&::before {
			backdrop-filter: blur(0);
		}
	}
	&__ctrl {
		position: absolute;
		left: 112px;
		//top: 529px + 40;
		bottom: 420px;
		z-index: 100;
		display: flex;
		flex-direction: row;
		align-items: center;
		@include breakpoint($mdw){
			left: 10px;
			bottom: 300px;
		}
		@include breakpoint($sm){
			display: none;
		}
		.swiper-pagination-bullet {
			@include rect(20px);
			background-color: #fff;
			opacity: 1;
			transform: translateY(3px);
		}
		.swiper-pagination-bullet-active {
			width: 48px;
			border-radius: 100px;
			background: var(--CI_blue, #15b4df);
		}
		button {
			@include rect(36px);
			line-height: 36px;
			background-color: transparent;
		}
		.section1-visual__swiper-button-stop {
			&::before {
				@include font-ico-outline("\e034");
				font-size: 32px;
				color: #fff;
				margin-top: 1px;
			}
		}
		.section1-visual__swiper-button-play {
			&::before {
				@include font-ico("\e037");
				font-size: 32px;
				color: #fff;
				margin-top: 1px;
			}
		}
		.section1-visual__swiper-button-prev {
			&::before {
				@include font-ico-outline("\e314");
				font-size: 35px;
				color: #fff;
			}
		}
		.section1-visual__swiper-button-next {
			&::before {
				@include font-ico-outline("\e315");
				font-size: 35px;
				color: #fff;
			}
		}
	}
	.swiper-pagination {
		position: static;
		color: #fff;
		//color: rgba(255, 255, 255, 0.8);
		text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.3);
		//font-size: rem(27);
		font-weight: 300;
		&-current {
			font-size: rem(20);
			font-weight: 700;
			color: rgba(255, 255, 255, 1);
		}
		&-total {
			font-size: rem(18);
			margin-left: 82px;
		}
		span {
			display: inline-block;
			padding: 0 10px;
		}
	}
	.autoplay-progress {
		position: absolute;
		top: 50%;
		left: calc(78px + 4px);
		width: 82px;
		height: 4px;
		transform: translateY(-50%);
		background: rgba(255,255,255,0.6);
		@include breakpoint($sm) { 
			left: calc(78px + 20px);
		}
		.progress {
			display: block;
			width: 0px;
			height: 4px;
			background: #39A0FF;
			&.active {
				animation: 10s progress linear;//6
			}
			&.first {
				animation: 10s progress linear;//5
			}
		}
	}
}
.s1-right-set {
	position: absolute;
	right: 0;
	bottom: 0;
	//width: 600px;
	width: 480px;
	max-width: 100%;
	height: 113px;
	background: rgba(0, 0, 0, 0.70);
	backdrop-filter: blur(4px);
	z-index: 30;
	display: flex;
	align-items: center;
	justify-content: space-between;
	a {
		width: calc(100% / 2);
		position: relative;
		padding: 20px 24px;
		color: #fff;
		display: block;
		@include breakpoint($sm){
			padding: 0 10px;
			text-align: center;
		}
		&:nth-child(2) {
			border-width: 1px 0;
			@include breakpoint($sm){
				border: none;
			}
		}
		& + a {
			&::before {
				content: "";
				display: inline-block;
				position: absolute;
				top: 50%;
				left: 0;
				background: rgba(255, 247, 247, 0.20);
				width: 1px;
				height: 65px;
				margin-top: -32px;
			}
		}
	}
	strong {
		display: block;
		font-weight: 500;
		&::after {
			@include font-ico('\e315');
			font-size: 18px;
			color: #fff;
			float: right;
			@include breakpoint($sm){
				display: none;
			}
		}
	}
	.t {
		display: block;
		font-size: rem(24);
		font-weight: 300;
		text-align: right;
		height: 49px;
		@include breakpoint($sm){
			font-size: rem(20);
		}
		em {
			font-size: rem(44);
			font-weight: 600;
			line-height: 1.4;
			margin-right: 8px;
			@include breakpoint($sm){			
				font-size: rem(30);
			}
		}
		.ctype1 {
			color: #FF327C;
		}
		.ctype2 {
			color: #39A0FF;
		}
	}
}

