	background-size: cover;
}

#section2 {
	position: relative;		
	background: #F2F6FA;
	background-size: auto;
	overflow: hidden;
	@include breakpoint($lg){
		&::before {
			content: "";
			display: inline-block;
			background-color: #DEE7F7;
			width: 800px;
			height: 300%;
			position: absolute;
			top: 0;
			right: 50%;
			margin-right: 76px;
			background-image: url(/static/portal/img/main/bg_section2.png);
			background-repeat: no-repeat;
			background-position: 0 0;
		}
	}	
	.set {
		padding: 100px 0;
		@include breakpoint($sm){
			padding: 0 0 50px; //수정
		}
	}
	h2 {
		position: relative;
		z-index: 9;
		//margin-top: -90px;
		margin-bottom: 30px !important;
	}
	.swiper {
		overflow: visible;
		margin-left: -6%;
		@include breakpoint($sm){
			margin-left: 0;	//추가
		}
		.ctrl-set {
			.swiper-pagination {
				display: inline-block;
				width: auto;
				height: 50px;
				position: absolute;
				top: -148px;
				left: 38.8%;
				z-index: 9;
				font-size: rem(24);
				color: #6282D0;
				font-weight: 300;
				&-current {
					color: #233B76;
					font-size: rem(36);
					font-weight: 700;
				}
				&-total {				
					font-size: rem(24);				
				}
				@include breakpoint($sm){
					display: none;
				}
			}
			.ctrl {
				position: absolute;
				top: -100px;
				right: 0;		
				@include breakpoint($sm){
					position: static;	//추가
				}		
				button {
					width: 48px;
					height: 48px;
					line-height: 48px;
					vertical-align: middle;
					@include breakpoint($sm){
						position: absolute;
						top: 50%;
						margin-top: -24px;
						z-index: 9;
					}
					&::before {
						content: "";
						display: inline-block;
						//vertical-align: middle;
						width: 48px;
						height: 48px;
					}
					&.prev,
					&.next {
						border: 1px solid $point;
						border-radius: 50%;
						&::before {
							background: url(/static/portal/img/main/ctrl-off2.png) 50% no-repeat;
						}
						&:hover {
							background-color: $point;
							&::before {
								background: url(/static/portal/img/main/ctrl-on2.png) 50% no-repeat;
							}
						}
					}
					&.prev {
						@include breakpoint($sm){
							left: 0;
						}
						&:hover {
							&::before {
								transform: rotate(180deg);
							}
						}
					}
					&.next {
						margin-left: 20px;	//정지, 시작 버튼 없을 경우
						@include breakpoint($sm){
							right: 0;
						}
						&::before {
							transform: rotate(180deg);
						}
						&:hover {
							&::before {
								transform: rotate(0);
							}
						}
					}
					&.stop,
					&.play {						
						color: #1C1B1F;
						&::before {							
							font-size: 22px;
							margin-bottom: -14px;
						}
					}
					&.stop {
						&::before {
							vertical-align: bottom !important;
							@include font-ico-outline("\e034");
						}
					}
					&.play {
						&::before {
							@include font-ico("\e037");
						}
					}
				}
			}
		}
		.swiper-wrapper {			
			.swiper-slide {
				width: 720px;
				a {
					width: 360px;
					display: block;
					padding: 16px;
					background-color: #fff;
					border-radius: 16px;
					box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.15);
				}
			}
			.swiper-slide-active {
				a {
					width:auto;
					margin: 0;
					background-color:transparent;
					box-shadow:none;
				}
			}		
			.swiper-slide-prev a {
				margin-left: 360px;
