			width: 40px;
			height: 40px;
			margin-top: -20px;
			&::before {
				width: 40px;
				height: 40px;
			}
		}
		&:hover {
			&::before {
				background: $point url(/static/portal/img/main/ctrl-on.png) 50% no-repeat;
				border-color: $point;
			}
		}
		&.prev {
			left: -100px;
			//left: 100px;
			@include breakpoint($smd){
				left: -35px;
				z-index: 9;
				background-color: rgba(255, 255, 255, 0.6);
				border-radius: 50%;
			}
			@include breakpoint($sm){
				left: -16px;
			}
			&:hover {
				&::before {
					transform: rotate(180deg);
				}
			}
		}
		&.next {
			right: -100px;
			//right: 100px;
			@include breakpoint($smd){
				right: -35px;
				z-index: 9;
				background-color: rgba(255, 255, 255, 0.6);
				border-radius: 50%;
			}
			@include breakpoint($sm){
				right: -16px;
			}
			&::before {
				transform: rotate(180deg);
			}
			&:hover {
				&::before {
					transform: rotate(0);
				}
			}
		}
	}		
}

@mixin board-list {
	padding: 50px 0;
	@include breakpoint($sm){
		padding: 30px 0;
	}	
	.more {
		position: absolute;
		top: 0;
		right: 0;
		z-index: 99;
		a {
			display: block;
			&::before {
				content: "";
				display: inline-block;
				width: 36px;
				height: 36px;
				background: url(/static/portal/img/main/more2.png);
			}
		}
	}
	.swiper-wrapper {
		.swiper-slide {
			position: relative;
			border-radius: 16px;
			padding: 16px;
			background-color: #fff;
			box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.20);
			overflow: hidden;
			@include breakpoint($smd){
				padding: 16px 16px 70px;
			}
			span {
				&.date {
					margin-top: 35px;
					color: #666;
					font-size: rem(14);
					@include breakpoint($sm){
						margin-top: 15px;
					}
				}
			}			
			@include breakpoint($lg){
				.bottom {
					opacity: 0;
				}
				&:hover,
				&.hover {
					padding: 16px 16px 80px;
					.bottom {
						opacity: 1;
					}
					.img-set {
						.img {
							padding-top: 63.8%;
							background-position: 50%;
							transition: all .5s;
							@include breakpoint($smd){
								padding-top: 63.8%;  //(기능 살림) 기능 삭제 전
								padding-top: 100%;
								background-position: 50%;
							}
						}
					}
					span {
						&.txt {
							@include line-clamp(6);
							height: 146px;
							@include breakpoint($smd){
								@include line-clamp(4);
								height: 100px;
							}
						}
					}
				}	
				span {
					&.txt {
						@include line-clamp(5);
						height: 120px;
						transition: all .5s;
					}
				}
				.bottom {
					opacity: 0;
					@include breakpoint($smd){
						opacity: 1;
					}
				}
			}
			@include breakpoint($sm){
				span {
					&.txt {
						height: 99px;
					}
				}
			}
			.bottom {
				@include board-bottom();
			}
			.img-set {
				position: relative;
				display: block;
				.img {
					display: block;
					background-size: cover;
					padding-top: 100%;
					border-radius: 16px 16px 0 16px;
					@include breakpoint($sm){
						padding-top: 70%;
					}
				}
				.p {
					color: #3B63C4;
					border-radius: 0 8px 0 0;
					padding: 5px 16px;
					background-color: #fff;
					position: absolute;
					bottom: 0;
					left: -1px;
				}
			}
			span {
				display: inline-block;
				&.title {
					margin: 20px 0 10px;
					padding: 0 16px;
					width: 100%;
					color: $black;
					font-size: rem(18);
					font-weight: 500;
					text-align: center;
					//@include ellipsis;
					@include line-clamp(2);
					height: 60px;
				}
				&.txt {
					padding: 0 16px;
					display: block;
					color: #4D4D4D;
					font-size: 16px;
					@include line-clamp(4);
					height: 97px;
				}
			}
		}
	}
}

