.mouseWrap{
	position: fixed;
	left: 50%;
	bottom: 10px;
	transform: translateX(-50%);
	z-index: 100;
	opacity: 1;
	transition: 0.5s;
	@include breakpoint($smd) {
		display: none;
	}
	.mouseTxt {
		margin-top: 12px;
		letter-spacing: 2px;
		text-indent: 2px;
		color: #fff;
		font-size: 14px;
	}
	.mouse {
		position: relative;
		width: 28px;
		height: 42px;
		border-radius: 100px;
		background-size: 100% 200%;
		animation: nudgeMouse 5s ease-out infinite;
		margin: 0 auto;
		&:before,
		&:after {
			content: "";
			position: absolute;
			top: 0; right: 0; bottom: 0; left: 0;
			margin: auto;
		}
		&:before {
			width: 28px - 6px;
			height: 42px - 6px;
			border-radius: 100px;
			border: 3px solid #fff;
		}
		&:after {
			background-color: #fff;
			width: 6px;
			height: 6px;
			border-radius: 100%;
			animation: trackBallSlide 5s linear infinite;
		}
	}
}

@keyframes trackBallSlide {
	0% { opacity: 1; transform: scale(1) translateY(-12px);}
	6% { opacity: 1; transform: scale(0.9) translateY(3px);}	//(12px/4)
	14% { opacity: 0; transform: scale(0.4) translateY(12px*2); }
	15%, 19% { opacity: 0; transform: scale(0.4) translateY(-12px); }
	28%, 29.99% { opacity: 1; transform: scale(1) translateY(-12px); }
	30% { opacity: 1; transform: scale(1) translateY(-12px); }
	36% { opacity: 1;transform: scale(0.9) translateY(3px);}	//(12px/4)
	44% { opacity: 0; transform: scale(0.4) translateY(12px*2); }
	45%, 49% { opacity: 0; transform: scale(0.4) translateY(-12px); }
	58%, 59.99% { opacity: 1; transform: scale(1) translateY(-12px); }
	60% { opacity: 1; transform: scale(1) translateY(-12px); }
	66% { opacity: 1;transform: scale(0.9) translateY(3px);}	//(12px/4)
	74% { opacity: 0; transform: scale(0.4) translateY(12px*2); }
	75%, 79% { opacity: 0; transform: scale(0.4) translateY(-12px); }
	88%, 100% { opacity: 1; transform: scale(1) translateY(-12px); }
}

@keyframes nudgeMouse {
	0% { transform: translateY(0); }
	20% { transform: translateY(8px); }
	30% { transform: translateY(0); }
	50% { transform: translateY(8px); }
	60% { transform: translateY(0); }
	80% { transform: translateY(8px); }
	90% { transform: translateY(0); }
}

