.section-nav {
position: fixed;
left: 32px;
bottom: 50%;
z-index: 200;
display: flex;
flex-wrap: wrap;
flex-direction: column;
align-items: center;
gap: 16px;
transition: all 0.3s;
}
@media (max-width: 1199px) {
.section-nav {
display: none;
}
}
.section-nav a {
display: flex;
padding: 7px;
color: rgba(255, 255, 255, 0.8);
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: normal;
text-transform: uppercase;
transition: all 0.5s;
}
.section-nav a * {
transition: all 0.5s;
}
.section-nav a i {
position: relative;
width: 6px;
height: 6px;
border-radius: 6px;
background-color: rgba(255, 255, 255, 0.6);
}
.section-nav a.active {
transition: none;
animation: pulse-white 2s infinite;
border-radius: 50%;
}
.section-nav a.active i {
width: 10px;
height: 10px;
border-radius: 10px;
background-color: #fff;
}
.section-nav a.active i::before {
content: "";
position: absolute;
inset: 0;
outline: 8px solid rgba(255, 255, 255, 0.2);
border-radius: 20px;
}
.section-nav--color a i {
background-color: rgba(59, 99, 196, 0.6);
}
.section-nav--color a.active {
animation: pulse-blue 2s infinite;
}
.section-nav--color a.active i {
background-color: rgb(59, 99, 196);
}
.section-nav--color a.active i::before {
outline: 8px solid rgba(59, 99, 196, 0.2);
background-color: rgb(59, 99, 196);
}

@keyframes pulse-white {
0% {
transform: scale(0.95);
box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.7);
}
70% {
transform: scale(1);
box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
}
100% {
transform: scale(0.95);
box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
}
}
@keyframes pulse-blue {
0% {
transform: scale(0.95);
box-shadow: 0 0 0 0 rgba(59, 99, 196, 0.7);
}
70% {
transform: scale(1);
box-shadow: 0 0 0 10px rgba(59, 99, 196, 0);
}
100% {
transform: scale(0.95);
box-shadow: 0 0 0 0 rgba(59, 99, 196, 0);
}
}
