.mouseWrap {
position: fixed;
left: 50%;
bottom: 10px;
transform: translateX(-50%);
z-index: 100;
opacity: 1;
transition: 0.5s;
}
@media (max-width: 1199px) {
.mouseWrap {
display: none;
}
}
.mouseWrap .mouseTxt {
margin-top: 12px;
letter-spacing: 2px;
text-indent: 2px;
color: #fff;
font-size: 14px;
}
.mouseWrap .mouse {
position: relative;
width: 28px;
height: 42px;
border-radius: 100px;
background-size: 100% 200%;
animation: nudgeMouse 5s ease-out infinite;
margin: 0 auto;
}
.mouseWrap .mouse:before, .mouseWrap .mouse:after {
content: "";
position: absolute;
top: 0;
right: 0;
bottom: 0;
left: 0;
margin: auto;
}
.mouseWrap .mouse:before {
width: 22px;
height: 36px;
border-radius: 100px;
border: 3px solid #fff;
}
.mouseWrap .mouse:after {
background-color: #fff;
width: 6px;
height: 6px;
border-radius: 100%;
animation: trackBallSlide 5s linear infinite;
}

@keyframes trackBallSlide {
